body{
    overflow-x: hidden;

}
body *{
    transition: .5s background-color;
}
.navbar{
    flex-direction: row !important;
    justify-content: space-between;
}

.navbar.light{
    background-color: var(--white);
    color: var(--dark);

}
.navbar.dark{
    background-color: var(--dark-2);
    color: var(--white);
}

.navbar.dark .icon{
    color:rgb(255, 185, 21);
}
.containerOfList{
    min-height:1000vh;
    width: 100vw;
    position: relative;
}
.containerOfList.light{
    background-color: var(--white-2);
}
.containerOfList.dark{
    background-color: var(--dark);
}

.center{
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
}



.containerOfList.dark .card{
    background-color: var(--dark-2);
    color:var(--white);
}

.containerOfList.dark .player{
    background-color: var(--dark-2) !important;
    color:var(--white-2);
}
.containerOfList.dark .player .controll *{
    color:var(--white) !important;
}

.player{   
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom:0;
    left:0;
    width:100vw;
    max-height: 200px;
    background-color: var(--white);
}




.player .info{
    display: flex;
    cursor: pointer;
    width:66%;
    overflow: auto;

}
.player .controll{
    display: flex;
    margin-right: 3rem;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    
}
.player .controll .icon{
    font-size:3rem;
    color:var(--dark);
}

.search-input{
    margin:20px 10px !important;
    width:80%;
}

.player-lg{
height: 100vh;
width:  100vw;

background-color: var(--dark);

display: flex;
flex-direction: column;
color:#fff;

z-index: 1;
position: fixed;
top:0;
left:50%;
transform: translateX(-50%);

animation: fromright .5s;
}

.player-lg  .container-image{

    padding:0 4rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 60%;

}
.player-lg  .container-image img{
    object-fit: cover;
    width:min(80% ,400px);

}
.player-lg .container-controls{
    height: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.player-lg .container-controls .controls{
    display: flex;
    justify-content: center;

    /* background-color: var(--white); */
    width: 100%;
    height:30%;
}

.player-lg .container-controls .controls .icon{

    color: var(--white);
   font-size: 3rem;
}

a{
    color:inherit !important;
    text-decoration: inherit !important;
}

.player-lg .icon-back{
    position: absolute;
    top:20px;
    left:20px;
   font-size: 2rem;

}


@keyframes fromright {
    from{
        position: fixed;
        left:100%;

    }
    to{
        position: fixed;

        left:50%;
    }
}